import React, { useState } from "react";

function MemberPage(props) {
    const [member] = useState(["18", "19", "20", "21"]);
    return (
        <>
            <div
                //   css
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    padding: "10px",
                    boxSizing: "border-box",
                    backgroundColor: "#fff",
                    width: "100%",
                    height: "65px",
                    color: "#fff",
                }}
            >
                <h1 style={{ fontSize: "2vmax" }}>{props.title}</h1>
            </div>
                <p
                    style={{
                        margin: "0",
                        backgroundColor: "#666",
                        color: "#fff",
                        padding: "10px",
                    }}
                >
                    {member.map((a, i) => {
                        return (
                            <>
                                <MemberRect member={member[i]} />
                            </>
                        );
                    })}
                </p>
        </>
    );
}

function MemberRect(props) {
    return (
        <>
            <p className="RectText">{props.member}</p>
            <hr />
            {props.member === "18" ? (
                <>
                    <div className="Rectangle" style={{ width: "28vmax" }}>
                         |  |  |  |  |  |
                        
                    </div>
                    <br />
                </>
            ) : (
                void 0
            )}
            {props.member === "19" ? (
                <>
                    <div className="Rectangle" style={{ width: "20vmax" }}>
                         |  |  |  | 
                    </div>
                    <br />
                </>
            ) : (
                void 0
            )}

            {props.member === "20" ? (
                <>
                    <div className="Rectangle" style={{ width: "16vmax" }}>
                         |  |  | 
                    </div>
                    <br />
                </>
            ) : (
                void 0
            )}
            {props.member === "21" ? (
                <>
                    <div className="Rectangle" style={{ width: "20vmax" }}>
                         |  |  |  | 
                    </div>
                    <br />
                </>
            ) : (
                void 0
            )}
        </>
    );
}

export default MemberPage;